<template>
  <div class="date-picker" @click="pickerFocus" v-click-outside="pickerBlur">
    <Input
      class="date-picker__input"
      hide-shadow
    >
      <template #prepend>
        <Icon name="calendar" />
      </template>
    </Input>
    <v-date-picker
      v-show="showPicker"
      ref="datePicker"
      :model-value="modelValue"
      :columns="2"
      color="orange"
      :first-day-of-week="1"
      :masks="{
          title: 'MMM YYYY',
          weekdays: 'WWW'
        }"
      :locale="locale"
      :is-range="isRange"
      class="date-picker__picker"
    />
  </div>
</template>

<script>
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import clickOutside from '@/directives/clickOutside'
import { ref } from 'vue'

export default {
  name: 'DatePicker',
  components: { Icon, Input },
  directives: {
    clickOutside
  },
  props: {
    isRange: {
      type: Boolean,
      default: false
    },
    modelValue: {
      type: Date,
      default: null
    }
  },
  setup () {
    const showPicker = ref(false)

    const pickerFocus = () => {
      console.log('CLICK')
      showPicker.value = true
    }
    const pickerBlur = () => {
      console.log('OUTSIDE')
      showPicker.value = false
    }

    return {
      showPicker,
      pickerBlur,
      pickerFocus
    }
  }
}
</script>

