<template>
  <div class="grid" :style="{ '--max-width': maxWidth + 'px' }">
    <div class="grid__head">
      <div
        v-for="item in columns"
        :key="item.id"
        class="grid__head__column grid__column"
        :style="{ '--width': item.width + 'px' }"
      >
        <slot name="column" :column="item" />
      </div>
    </div>
    <div class="grid__divider" />
    <div class="grid__body">
      <div
        v-for="item in data"
        :key="item.id"
        class="grid__row"
      >
        <div
          v-for="col in columns"
          :key="col.id"
          class="grid__row__cell grid__column"
          :style="{ '--width': col.width + 'px' }"
        >
          <slot name="cell" :item="item[col.name]" :column="col" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'Grid',
  props: {
    columns: {
      type: Array,
      default: () => []
    },
    data: {
      type: Array,
      default: () => []
    }
  },
  setup (props) {
    const maxWidth = computed(() => props
      .columns
      .reduce((acc, item) => acc + item.width, 0))
    return {
      maxWidth
    }
  }
}
</script>

<style lang="scss">
.grid {
  overflow-x: auto;

  &__column {
    width: var(--width);
    max-width: var(--width);
    min-width: var(--width);
    padding: 14px 0 14px 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  &__head {
    display: flex;

    &__column {
      padding-top: 8px;
      padding-bottom: 8px;

      &:first-child {
        margin-left: 20px;
        padding-left: 0;
      }
    }
  }
  &__body {
  }
  &__divider {
    width: var(--max-width);
    height: 2px;
    margin-left: 20px;
    background: rgba(0, 0, 0, 0.1);
  }
  &__row {
    display: flex;
    width: calc(var(--max-width) + 20px);
    border-radius: 4px;

    &:hover {
      background: #F3F5F9;
    }
    &__cell:first-child {
      margin-left: 20px;
      padding-left: 0;
    }
  }
}
</style>
